import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import CardUi from '@/pages/CardUi'
import Piece from '@/pages/Piece'
import Timer from '@/pages/Timer'
import TodoList from '@/pages/TodoList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/card-ui',
      name: 'CardUi',
      component: CardUi
    },
    {
      path: '/piece',
      name: 'Piece',
      component: Piece
    },
    {
      path: '/timer',
      name: 'Timer',
      component: Timer
    },
    {
      path: '/todo-list',
      name: 'TodoList',
      component: TodoList
    },
    {
      path: '/test-list',
      name: 'TestList',
      component: TodoList
    }
  ],
  mode: 'history'
})



