<template>
  <div class="home">
    <router-link v-for="route in routes" :key="route.name"
                 exact-active-class="on"
                 tag="span"
                 class="link"
                 :to="{name: route.name}">
      {{route.name}}
    </router-link>
  </div>
</template>
<script>
  export default {
    name: 'Home',
    computed: {
      routes() {
        return this.$router.options.routes
      }
    }
  }
</script>
<style lang="scss" scoped>
  .home {
    width: 100%;
    height: 100%;
    .link {
      cursor: pointer;
      &.on {
        color: #0000ff;
      }
    }
  }
</style>



